.classCard {
    background-size: cover;
    background-position: center;
    position: relative;
    padding: 20px;
    color: white;
    width: 340px;
    height: 420px;
  }
  
  .classCardHeaderDiv {
    text-align: center;
    width: 100%;
    margin-bottom: 30px;
  }
  
  h2 {
    margin: 0;
    padding-bottom: 15px;
    font-family: "JetBrainsMono";
    font-weight: 100;
  }
  
  .registerButton {
    background-repeat: no-repeat;
    width: 340px;
    display: flex;
    align-items: center;
    height: 60px;
    margin-bottom: 40px;
  }
  
  .buttonImage {
    width: 300px;
    height: 130px;
  }
  
  .buttonOverlay {
    position: relative;
    font-size: large;
    font-family: "JetBrainsMono";
    font-weight: 100;
  }
  
  .buttonOverlay img {
    display: block;
    width: 100%; /* Make the image fill the container */
  }
  
  .registerButtonDiv {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 130px;
    width: 380px;
    align-items: center;
    align-content: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  
  /* Shade in the image when hovering over it */
  .buttonOverlay img:hover {
    opacity: 0.5;
  }
  
  .buttonOverlay p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    color: white; /* Change text color as needed */
    font-size: 18px; /* Adjust font size as needed */
    font-weight: bold;
  }